<template>
  <div id="app">
    <Header></Header>
    <div id="content">
    <div id="bigBox">
    <router-view
    class="view"
    keep-alive
    transition
    transition-mode="out-in">
    </router-view>
    <!-- <p>{{Username}}</p> -->
    </div>
    </div>
  </div>
</template>

<script>
import './jquery-1.10.2.js'
import './scrollWindow.js'
import Header from './Header.vue'
window.onload = function(){
var windowScroll = new KG.LRL.srollWindow({
  windowPage:document.getElementById('content'),
  outsideBox:document.getElementById('bigBox'),
  marginTop:67
})
windowScroll.init();
}
console.log($('#nav').css('height'))
export default {
  data(){
    return {
      Username:'',
      Password:''
    }
  },
  components:{
    Header,
  },
  methods: {
  }
}
</script>
<style>
body{
  margin:0;
}
#app{
  text-align: center;
}
#content{ 
  overflow: hidden;
  padding-top: 67px;
}
#bigBox{
  position: relative;
  top: 0;
  left: 0;
  height: auto;
}
#box2{
  width: 100%;
  background-color: red;
}
#box3{
  width: 100%;
  background-color: blue;
}
#box4{
  width: 100%;
  background-color: rgb(0, 255, 98);
}
#box1{
  width: 100%;
  background-color: #ccc;
}
</style>



