import './common.js'
KG.LRL.srollWindow = (function(){
    function obj(options){
    this._setParam(options);
    }
    obj.prototype = {
        constructor: obj,
        // 初始化对象
        init:function(){
            var _this = this;
            this._setDivSize();
            $(window).resize(function(){
                // 窗口大小改变响应容器大小改变
                _this.pageHeight=$(window).height()-_this.marginTop;
                $(_this.outsideBox).css('top',-_this.scrollTimes*_this.pageHeight)
                _this._setDivSize();
              });
            this._bindScroll();
        },
        //绑定窗口滚动时间
        _bindScroll:function(){
            var _this = this;
            $('html').bind("mousewheel",function(event){
            if(_this.animateState){
                if(event.originalEvent.deltaY>0){
                    _this.scrollTimes++;
                    if(_this.scrollTimes>=$(_this.outsideBox).children().length-1){
                        _this.scrollTimes=$(_this.outsideBox).children().length-1;
                    }

                }else{
                    _this.scrollTimes--;
                    if(_this.scrollTimes<0){
                        _this.scrollTimes=0;
                        return false}
                }
                _this.animateState=false;
                $(_this.outsideBox).stop().animate({
                    top:-_this.scrollTimes*_this.pageHeight +'px'
                },1000,function(){
                    _this.animateState = true;
                    $(_this.outsideBox).clearQueue()
                })
            }
            })
            
        },
        _setDivSize:function(){
            // 设置容器大小
            var _this = this;
            $(this.windowPage).css('height',this.pageHeight+'px')
            $(this.innerBox).each(function(){
            $(this).css('height',_this.pageHeight+'px')
            })
        },
        _setParam:function(option){
            // 传参
            this.marginTop = option.marginTop;
            this.animateState = true;//对动画队列的控制
            this.pageHeight=window.innerHeight- this.marginTop;//窗口高度
            this.scrollTimes = 0;//滚轮滚动次数
            this.windowPage = option.windowPage;//窗口容器
            this.outsideBox = option.outsideBox;//外部容器
            this.innerBox = option.outsideBox.children;//容器中的滚动小容器
            
        }
    }
    return obj;
})($,window.KG)



