<template>
<div id="nav">
  <div id="nav-center">
    <div id="logo"></div>
    <el-row :gutter="20" :span="24">
    <el-col :span="2"><router-link to="/team" class="grid-content ">组队</router-link></el-col>
    <el-col :span="2"><router-link to="/act" class="grid-content ">活动</router-link></el-col>
    <el-col :span="2"><router-link to="/pro" class="grid-content ">项目</router-link></el-col>
    <el-col :span="2" class="fr"><router-link to="/register" class="grid-content ">注册</router-link></el-col>
    <el-col :span="2" class="login fr"><router-link to="/login" class="grid-content ">登录</router-link></el-col>
    </el-row>
    
  </div>
</div>
</template>

<script>
import Team from './team.vue'
import Pro from './project.vue'
import Act from './act.vue'
import Vue from 'vue'
import Login from './login.vue'
import Register from './register.vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)
    export default{
      components:{
            Team,
            Act,
            Pro,
            Login,
            Register
      }
    }
</script>
<style>
#nav{
  z-index: 1;
  width: 100%;
  position: fixed;
 font-family: "微软雅黑";
 overflow: hidden;
 border-bottom: 1px solid rgb(221, 221, 221);
 background-color:#fff;

}
#nav-center{
  width: 960px;
  margin: 0 auto;
  margin-top: 5px;
  margin-bottom: 5px;
}
#logo {
  float: left;
  width:65px;
  height:32px;
  background: url('./assets/logob.png') no-repeat ;
  background-size: 100%;
}
 .el-row {
   color:rgb(50, 58, 69);
  }
  .el-col {
    line-height: 36px;
    border-radius: 4px;
    user-select:none;
  }
  .login{
    background: url('./assets/login.png') no-repeat;
    background-position: 4px 10px;
    background-size: 20%;
  }
  .login:hover{
    background: url('./assets/login-change.png') no-repeat;
    background-position: 4px 10px;
    background-size: 20%;
    transition: .5s ease;
  }
  .fl{
    float:left;
  }
  .fr{
    float: right;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
    text-decoration: none;
  }
  .grid-content:hover{
    cursor: pointer;
    color:#359eff;
    transition: .5s ease;
  }
 
</style>



